import React, { useState, useEffect } from 'react';
import { 
  LineChart, Line, AreaChart, Area, 
  XAxis, YAxis, CartesianGrid, Tooltip, Legend, 
  ResponsiveContainer, RadarChart, Radar, PolarGrid, 
  PolarAngleAxis, PolarRadiusAxis, ReferenceLine, ReferenceArea,
  ComposedChart
} from 'recharts';


const BASE = process.env.PUBLIC_URL || '';

// Helper: rcupration JSON robuste avec messages explicites en cas de HTML ou d'erreur HTTP
async function safeFetchJson(url, what='resource') {
  const resp = await fetch(url, { cache: 'no-cache' });
  if (!resp.ok) {
    let sample = '';
    try { sample = (await resp.text()).slice(0,180).replace(/\n/g,' '); } catch {}
    throw new Error(`HTTP ${resp.status} pour ${what} (${url}). Dbut: ${sample}`);
  }
  // Certains serveurs ne mettent pas toujours le content-type JSON
  try {
    return await resp.json();
  } catch (e) {
    let sample = '';
    try { sample = (await resp.text()).slice(0,180).replace(/\n/g,' '); } catch {}
    throw new Error(`Rponse non-JSON pour ${what} (${url}). Dbut: ${sample}`);
  }
}
